import { Component, OnInit, Input, NgZone } from '@angular/core';
@Component({
  selector: 'app-skelton-listview',
  templateUrl: './skelton-listview.component.html',
  styleUrls: ['./skelton-listview.component.scss'],
})
export class SkeltonListviewComponent implements OnInit {
  @Input() iconPath:any;
  @Input() className:any;
  constructor(private zone:NgZone) { }
  skeletonPath:string='';
  iconClass:string='';
  ngOnInit(){
    this.zone.run(()=>{
      this.skeletonPath=this.iconPath;
      this.iconClass=this.className;
      console.log('list view skeletonPath== ', this.skeletonPath);
      console.log('list view iconClass== ', this.iconClass);
    })
  }
  
}