import { Component, OnInit, Input, NgZone } from '@angular/core';

@Component({
  selector: 'app-custom-sizebutton',
  templateUrl: './custom-sizebutton.component.html',
  styleUrls: ['./custom-sizebutton.component.scss'],
})
export class CustomSizebuttonComponent implements OnInit {
  @Input('btnText') btnText;
  @Input('iconPath') iconPath;
  @Input('className') className;
  @Input('size') buttonClass;
  // @Input('btnDisable') btnDisable;
  constructor(private zone:NgZone) { }
  buttonName:string='';
  buttonIconPath:string='';
  iconClass:string='';
  buttonSize:string='';
  ngOnInit() {}
  ngAfterContentInit(): void {
    //Called after ngOnInit when the component's or directive's content has been initialized.
    //Add 'implements AfterContentInit' to the class.
    this.zone.run(()=>{
      this.buttonName=this.btnText;
      this.buttonIconPath=this.iconPath;
      this.iconClass=this.className;
      this.buttonSize=this.buttonClass;
      console.log('buttonName== ', this.buttonName +' /buttonIconPath== '+ this.buttonIconPath + ' /iconClass== '+this.iconClass+ '/size',  this.buttonSize );
    })
  }

}
