import { Component, OnInit, Input, NgZone  } from '@angular/core';
import { PhotoViewer } from '@ionic-native/photo-viewer/ngx';

@Component({
  selector: 'app-image-view',
  templateUrl: './image-view.component.html',
  styleUrls: ['./image-view.component.scss'],
})

export class ImageViewComponent implements OnInit{

  @Input('img') img
  @Input('imageView') imageView
  image:any;
  class:any;

  constructor(private zone:NgZone,private photoViewer:PhotoViewer) { }

  







  ngOnInit(){}

  ngAfterContentInit(): void{
    //Called after ngOnInit when the component's or directive's content has been initialized.
    //Add 'implements AfterContentInit' to the class.
    this.zone.run(()=>{
      this.image=this.img;
    })
  }

  viewImageFunc(){
    if(this.imageView==true){
      this.photoViewer.show(this.image);
      // this.photoViewer.show(this.image, "Benepik One", {headers: "{username:foo,password:bar}"});
    }else{}
  }









}